import React from 'react';
import PropTypes from 'prop-types';

function finder__results__profile(props) {
    const school = props.details.metaData.E && (
        <p className="finder__results__card__tag">
            <span className="fal fa-fw fa-university icon" aria-hidden="true" />{' '}
            {props.details.metaData.E}
            {props.details.metaData.D && `, ${props.details.metaData.D}`}
        </p>
    );

    const headshot = props.details.metaData.K && (
        <div className="thumbnail__image-container">
            <img src={props.details.metaData.K} alt="" />
        </div>
    );

    return (
        <li className="finder__results__card finder__results__profile">
            <a href={'/fb' + props.details.clickTrackingUrl}>
                {headshot}
                <div className="finder__results__card__details">
                    <h3 className="finder__results__card__heading underline-transition__title">
                        <span>{props.details.title}</span>
                    </h3>
                    <p className="finder__results__card__description">
                        {props.details.metaData.J}
                    </p>
                    {school}
                </div>
            </a>
        </li>
    );
}

finder__results__profile.propTypes = {
    details: PropTypes.object,
};

export default finder__results__profile;
