import React from 'react';
import PropTypes from 'prop-types';

function finder__appliedfilters(props) {
    const removeFacet = facet => {
        const params = new URLSearchParams(window.location.search),
            newQuery = props.query;

        params.delete(`meta_${facet}_orsand`);
        delete newQuery.facets[facet];

        window.history.replaceState(
            {},
            '',
            window.location.pathname + '?' + params.toString()
        );

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const facetKeys = Object.keys(props.query.facets);

    if (facetKeys.length) {
        return (
            <ul className="finder__appliedfilters">
                {facetKeys.map(facet => {
                    const labels = props.config.facetLabels.filter(
                            facetLabel => facetLabel.meta === facet
                        )[0],
                        facetValue = props.query.facets[facet],
                        label = labels.values.filter(
                            value => value.data === facetValue
                        )[0];

                    return (
                        <li key={facet}>
                            <button
                                type="button"
                                onClick={() => removeFacet(facet)}
                            >
                                <span
                                    className="fal fa-times icon"
                                    aria-label="Remove filter"
                                />{' '}
                                <span>{`${labels.name}: ${label.label}`}</span>
                            </button>
                        </li>
                    );
                })}
            </ul>
        );
    } else {
        return null;
    }
}

finder__appliedfilters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
};

export default finder__appliedfilters;
